<script>
import {gsap} from "gsap/all";
import {ScrollTrigger} from "gsap/ScrollTrigger";


export default {
  name: "Detail2",
  data() {
    return {
      speed: [-9, -3, 5, -7, -5, 2, -9]
    }
  },
  mounted() {
// ball
    gsap.set(".ball", {xPercent: -50, yPercent: -50});
    const ball = document.querySelector(".ball");
    const pos = {x: window.innerWidth / 2, y: window.innerHeight / 2};
    const mouse = {x: pos.x, y: pos.y};
    const mouseSpeed = 0.2;
    const xSet = gsap.quickSetter(ball, "x", "px");
    const ySet = gsap.quickSetter(ball, "y", "px");
    window.addEventListener("mousemove", e => {
      mouse.x = e.x;
      mouse.y = e.y;
    });
    gsap.ticker.add(() => {
      const dt = 1.0 - Math.pow(1.0 - mouseSpeed, gsap.ticker.deltaRatio());
      pos.x += (mouse.x - pos.x) * dt;
      pos.y += (mouse.y - pos.y) * dt;
      xSet(pos.x);
      ySet(pos.y);
    });

// section1
    const bubble = gsap.from('.section1 .bubble', {
      scale: 0,
      opacity: 0,
      paused: true,
      stagger: 0.1, //ë”°ë¡œë”°ë¡œ ì¼œì§€ê²Œ ì„¤ì •
    });

    gsap.registerPlugin(ScrollTrigger);
    ScrollTrigger.create({
      trigger: '.section1',
      start: 'top 40%',
      end: 'bottom top',
      onEnter: () => bubble.play(),
    });


    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: '.section1',
        start: 'center center',
        end: 'bottom center',
        scrub: 5,
        pin: true,
        pinSpacing: true,
        toggleActions: 'play pause reverse reset'
      }
    })
    tl.to('.inner', {
      opacity: 0,
      y: '-100%',
      duration: 6,
    })


    const composeTl = gsap.timeline({
      scrollTrigger: {
        trigger: ".active-compose.first",
        start: "center center",
        end: '+=1000',
        scrub: 1,
        pinSpacing: true,
        pin: true,
        toggleActions: "play pause reverse reset",
      }
    });

    composeTl.to(".active-compose.first .text-step1", {
      opacity: 0,
      y: "-100%",
      duration: 6,
    })
      //img1
      .to(".active-compose.first .img-step1", {
        opacity: 1,
        x: "-100%",
        scale: 0.8,
        duration: 6,
      }, "-=6")

      //2
      .to(".active-compose.first .text-step2", {
        opacity: 1,
        y: "0%",
        duration: 6,
      })
      //img2
      .to(".active-compose.first .img-step2", {
        opacity: 1,
        y: "0%",
        duration: 6,
      }, "-=3")

      //3
      .to(".active-compose.first .text-step2", {
        opacity: 0,
        y: "-100%",
        duration: 6,
      }, "+=10")
      .to(".active-compose.first .img-step2", {
        opacity: 1,
        x: "-86%",
        scale: 0.7,
        duration: 6,
      })
      .to(".active-compose.first .img-step1", {
        x: "-150%",
        scale: 0.3,
        duration: 3,
      }, "-=6")
      .to(".active-compose.first .text-step3", {
        opacity: 1,
        y: "0%",
        duration: 6,
      }, "+=3")

      .to(".active-compose.first .img-step3", {
        opacity: 1,
        y: "0%",
        duration: 6,
      }, "-=3");

// secondo anim
    gsap.to(".animated-test", {
      x: 100,
      scrollTrigger: {
        trigger: ".activate-anim",
        start: "top bottom",
        duration: 6,
      }
    })


// section 2
    const ARTICLE = gsap.utils.toArray('article')
    gsap.to(ARTICLE, {
      xPercent: -100 * (ARTICLE.length - 1),
      ease: 'none',
      scrollTrigger: {
        trigger: '.section2',
        end: () => "+=" + document.querySelector('.section2').offsetWidth,
        pin: true,
        scrub: 1,
        snap: 1 / (ARTICLE.length - 1)
      }
    })

// section 3
    gsap.registerPlugin(ScrollTrigger);
    gsap.to('.section3 .spread-text p', {
      backgroundPositionX: '0%',
      stagger: 1,
      scrollTrigger: {
        trigger: '.spread-text',
        scrub: 1,
        start: 'top center',
        end: 'bottom bottom'
      }
    })

  },
  methods: {
    bubbleMove(e) {
      this.speed.forEach((speed, idx) => {
        const x = (window.outerWidth - e.offsetX * speed) / 200;
        const y = (window.outerHeight - e.offsetY * speed) / 200;
        const bubble = document.querySelector(`.bubble-0${idx + 1}`)
        gsap.to(bubble, {x: x, y: y});
      })
    }
  }
}
</script>

